













































































var utils = require('../../common/utils').utils;
export default {
	data () {
		return {
			status_class: '',
			lists: [],
			alist: [], // 用户回复列表
			statu: '',
			unfold: false
		}
	},
	onLoad: function (options) {
		console.log(options)
		this.getReportDetails(options.fault_id);
		this.getReplyToList(options.fault_id)
	},
	methods: {
		getReportDetails(faultid) {
			//获取报修详情
			var _this = this;
			utils.requestGet("operation/getmodifydetail/"+ faultid, function (res) {
				if (res.code == 0) {
					var data = res.data[0];
					console.log(data.status);
					_this.statu = data.status
					data.ut = utils.formatTimeStr(data.ut)
					_this.status_class="header state"+data.status;
					_this.lists = data
				}
			})
		},
		getReplyToList(fault_id) {
			//获取回复列表
			var _this = this;
			var url = 'operation/getreplylist/'+ fault_id;
			utils.requestGet(url, function (res) {
				if (res.code == 0) {
					var datal = res.data
					for (var j in datal) {
						datal[j].ut = utils.formatTimeStr(datal[j].ut);
					}
					_this.alist = datal
				}
			})
		},
		/**
		 * 图片预览
		 */
		changePreview(urls){
			uni.previewImage({
				urls: urls
			});
		},
		unfoldMenu () {
			
		},
		/**
		 * 催单
		 */
		reminder: function () {
			var _this = this,
				data = _this.data.adminInfo;
			console.log(data);
			if (data.school_id && data.fault_id) {
				var url = 'https://www.' + util.host + '/usr/' +
					data.userid + '/' +
					data.session +
					'/operation/speedup/' +
					data.school_id + '/' +
					data.fault_id;
				console.log(url)
				wx.showModal({
					title: '催单',
					content: '确认催促检修员加速处理吗？',
					success: function (res) {
						if (res.confirm) {
							console.log('用户点击确定');
							wx.request({
								url: url,
								data: '',
								header: {},
								method: 'GET',
								dataType: 'json',
								responseType: 'text',
								success: function (res) {
									console.log(res)
									if (res.statusCode === 200 && res.data.code === 0) {
										console.log(res, '催单成功');
										wx.showToast({
											title: '催单成功!',
											icon: 'success'
										});
									} else if (res.data.code === -2) {
										wx.showToast({
											title: res.data.msg,
											icon: 'none',
											duration: 2000
										})
									}
								},
								fail: function (res) {
									console.log(res, '催单失败!')
								},
								complete: function (res) {},
							})
						} else if (res.cancel) {
							console.log('用户点击取消')
						}
					}
				})
			}
		},
		/**
		 * 是否需要评论  /usr/:userid/:session/operation/checkscored/:school_id/:recid
		 */
		commentroll: function () {
			var _this = this,
				data = this.data.adminInfo,
				url = 'https://www.' + util.host + '/usr/' +
				data.userid + '/' +
				data.session +
				'/operation/checkscored/' +
				data.school_id + '/' +
				data.fault_id
			if (data !== '') {
				wx.request({
					url: url,
					data: '',
					header: {},
					method: 'GET',
					dataType: 'json',
					responseType: 'text',
					success: function (response) {
						if (response.statusCode === 200 && response.data.code === 0) {
							var datum = response.data.data;
							if (datum.length === 0) {
								console.log('需要评论!');
								var commentUrl = '../comment/comment' +
									'?userid=' + data.userid +
									'&session=' + data.session +
									'&school_id=' + data.school_id +
									'&recid=' + data.fault_id;

								setTimeout(function () {
									wx.navigateTo({
										url: commentUrl,
									})
								}, 1000)
							} else {
								console.log('暂不需要评论!');
							}
						} else {
							console.log(response, '信息获取有误!')
						}
					},
					fail: function (res) {
						console.log(res, '获取是否需要评论接口失败!')
					},
					complete: function (res) {},
				})
			}
		},
		/**
		 * 投诉建议 /usr/:userid/:session/operation/postsuggest */
		complaint: function () {
			let _this = this,
				data = _this.data.adminInfo;
			if (data !== '') {
				var url = 'https://www.' + util.host + '/usr/' + data.userid + '/' + data.session +
					'/operation/postsuggest',
					path = '../complaint/complaint?submitpath=' + url + '&school_id=' + data.school_id + '&rec_id=' +
					data.fault_id;
				wx.navigateTo({
					url: path,
				})
			}
		}
	}
}



